n    & .header-logo {\r\n      width: 200px;\r\n    }\r\n  }\r\n}\r\n.nav-wrapper {\r\n  display: none;\r\n\r\n  @media screen and (min-width: 650px) {\r\n    gap: 1rem;\r\n    @include mixin.grid-item-col(2, 3);\r\n    @include mixin.flexbox(center, center, row);\r\n\r\n    & a {\r\n      text-decoration: none;\r\n      color: clr.$clr-primary;\r\n      font-weight: fonts.$fw-medium;\r\n      font-size: fonts.$fs-3;\r\n  \r\n      &:hover {\r\n        color: clr.$clr-hover;\r\n      }\r\n    }\r\n  }\r\n}\r\n.btn-dropdown {\r\n  background-color: transparent;\r\n  color: clr.$clr-primary;\r\n  font-weight: fonts.$fw-medium;\r\n  font-size: fonts.$fs-3;\r\n  gap: .5rem;\r\n  @include mixin.flexbox(center, center, row);\r\n\r\n  &:hover {\r\n    cursor: pointer;\r\n    color: clr.$clr-hover;\r\n  }\r\n\r\n  & i {\r\n    color: clr.$clr-primary;\r\n  }\r\n}\r\n.nav-dropdown {\r\n  position: relative;\r\n  gap: .5rem;\r\n  @include mixin.flexbox(center, start, row);\r\n\r\n  &::after {\r\n    content: \"\";\r\n    display: block;\r\n    height: 1rem;\r\n  }\r\n\r\n  & .dropdown-items {\r\n    // visibility: hidden;\r\n    position: absolute;\r\n    top: 0;\r\n    left: 100%;\r\n    gap: .5rem;\r\n    @include mixin.flexbox(center, center, row);\r\n\r\n    & a {\r\n      font-size: fonts.$fs-7;\r\n      font-weight: fonts.$fw-light;\r\n      gap: .5rem;\r\n      @include mixin.flexbox(center, center, row);\r\n    }\r\n  }  \r\n}\r\n.book-wrapper {\r\n  display: none;\r\n\r\n  @media screen and (min-width: 1000px) {\r\n    justify-self: flex-end;\r\n    @include mixin.grid-item-col(3, 4);\r\n    @include mixin.flexbox(center, center, row);\r\n\r\n    & button {\r\n      color: clr.$clr-primary;\r\n      font-weight: fonts.$fw-medium;\r\n      font-size: fonts.$fs-3;\r\n  \r\n      background-color: transparent;\r\n      border: 2px solid clr.$clr-primary;\r\n      border-radius: 10px;\r\n      padding-block: .75rem;\r\n      padding-inline: 1rem;\r\n  \r\n      &:hover {