 screen and (min-width: 650px) {\r\n    font-size: fonts.$fs-10;\r\n  }\r\n}\r\n.hero-subtitle {\r\n  text-align: center;\r\n  color: clr.$clr-primary;\r\n  font-weight: fonts.$fw-bolder;\r\n  font-size: fonts.$fs-5;\r\n  margin-bottom: .5rem;\r\n\r\n  @media screen and (min-width: 650px) {\r\n    font-size: fonts.$fs-8;\r\n    margin-bottom: 0;\r\n  }\r\n}\r\n.hero-tagline {\r\n  text-align: center;\r\n  color: clr.$clr-primary;\r\n  font-weight: fonts.$fw-light;\r\n  font-size: fonts.$fs-5;\r\n  margin-right: .25rem;\r\n\r\n  & a {\r\n    color: clr.$clr-primary;\r\n  }\r\n\r\n  @media screen and (min-width: 650px) {\r\n    font-size: fonts.$fs-8;\r\n    margin-right: .5rem;\r\n  }\r\n}\r\n.form-overlay {\r\n  position: absolute;\r\n  width: 90%;\r\n  top: 20%;\r\n  left: 5%;\r\n\r\n  background: rgba(0, 0, 0, 0.5);\r\n  border-radius: 16px;\r\n  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);\r\n  backdrop-filter: blur(6.1px);\r\n  -webkit-backdrop-filter: blur(6.1px);\r\n\r\n  grid-template-rows: 75px 1fr 1fr;\r\n\r\n  @media screen and (min-width: 650px) {\r\n    width: 80%;\r\n    left: 10%;\r\n    grid-template-rows: 75px 1fr;\r\n    @include mixin.grid-col(3, 1);\r\n  }\r\n\r\n  @media screen and (min-width: 800px) {\r\n    width: 70%;\r\n    left: 15%;\r\n  }\r\n\r\n  @media screen and (min-width: 1000px) {\r\n    width: 60%;\r\n    left: 20%;\r\n  }\r\n\r\n  @media screen and (min-width: 1200px) {\r\n    width: 50%;\r\n    left: 25%;\r\n  }\r\n}\r\n.form-title-wrapper {\r\n  padding: 1rem;\r\n  padding-inline: 3rem;\r\n  border-bottom: 1px solid rgba($color: clr.$clr-primary, $alpha: .2);\r\n  @include mixin.grid-item-col(1, 4);\r\n  @include mixin.grid-item-row(1, 2);\r\n  @include mixin.flexbox(space-between, center, row);\r\n\r\n  & .form-title {\r\n    font-size: fonts.$fs-6;\r\n    font-weight: fonts.$fw-bolder;\r\n    color: clr.$clr-primary;\r\n  }\r\n\r\n  & .form-back {\r\n    color: clr.$clr-primary;\r\n    text-decoration: none;\r\n    @include mixin